export default {   
	data() {
		return {
			tabInd:0,
			statusBarHeight:0,
			bodyHeight:0, 
			state:'', 
			isLocked: true, 
			// 库房
			storeroomArray:[],
			storeroomIndex:0,
			storeroomNum:0,
			storerooms:[],
			// 区
			regions:[],
			// 区名称
			regionArray:[],
			regionIndex:0,
			regionNum:0,	
			// 列
			cols:[],
			colIndex:0,
			 // 移动方向
			 direction:'',
			 // 当前状态
			 curState:'',
			 isMove:false,
			hjz:{
				wd: 0,
				sd: 0,
				co2: 0,
				ch20: 0,
				pm10: 0,
				pm25: 0,
				tvoc: 0,
			}, 
			timeer:null,
		};
	},
	watch: {
		storeroomNum: function(newVal, oldVal) {
			uni.setStorage({
			    key: 'fkStoredId',
			    data: newVal,
			    success: function () {
			        // console.log('success');
			    }
			});
		},
	},
	onLoad() {
		// 获取手机状态栏高度
		this.statusBarHeight = uni.getSystemInfoSync()['statusBarHeight'];
		// 获取实际body高度、宽度
		this.bodyHeight=uni.getSystemInfoSync()['windowHeight']-this.statusBarHeight-48; 

		this.selectStoreRoom();
	},
	methods: { 
		onPageJump(){ 
			uni.navigateTo({
				url: '../search/search' 
			}); 
		},
		scrollL(e){
			let scroll_x=e.detail.scrollLeft; 
			const query = uni.createSelectorQuery().in(this);
			query.select('.temp_shelf .item_box').boundingClientRect(data => { 
				var int=Math.floor(scroll_x/(data.width*3));
				this.tabInd=int; 
			}).exec(); 
		},
		upper(e){
			this.tabInd=0;
		},
		lower(e){
			this.tabInd=1;
		}, 
		// 查询所有库房
		selectStoreRoom:function(){
			let _this=this;
			_this.submitAjax('environmentmodule/wkStoTbStore/selectWkStoTbStore',null, 'GET', function(res) {
				_this.storeroomArray=[];
				_this.storerooms=res.data.rows; 
				for(let item of res.data.rows){
					_this.storeroomArray.push(item.storeName);
				} 
				_this.storeroomNum=res.data.rows[0].id; 
				_this.selectRegion(_this.storeroomNum); 
			})
		},
		// 库房改变
		storeroomChange:function(e){
			let _this=this;
			_this.storeroomNum=_this.storerooms[e.target.value].id;  
			_this.storeroomIndex=e.target.value;
			_this.selectRegion(_this.storeroomNum);
		},
		// 查询所有的区
		selectRegion:function  (fkStoreId) { 
			let _this=this;
			_this.submitAjax('storeroommodule/stoTbRegion/selectByBind?fkStoreId='+fkStoreId,null, 'GET', function(res) {
				_this.regionArray=[];
				_this.regions=res.data.rows; 
				for(let item of _this.regions){
					_this.regionArray.push(item.regionName);
				} 
				_this.regionNum=res.data.rows[0].regionNum; 
				_this.getCols(_this.regionNum);
				// 获取温湿度、状态
				_this.timeer=setInterval(()=>{
					_this.timingGetState()
				},3000);  
			})
		},
		regionChange:function(e){
			let _this=this;
			_this.regionNum=_this.regions[e.target.value].regionNum;  
			_this.regionIndex=e.target.value;
			_this.getCols(_this.regionNum);
		},
		// 根据区获取列
		getCols:function(qu_num){
			let _this=this;
			_this.cols=[];   
			let region={};
			for(let item of _this.regions){
				if(item.regionNum==qu_num){
					region=item;
					break;
				}
			}
			for(let i=1;i<=region.cols;i++){
				if((region.gdlType=='left') || (region.gdlType=='right')){
					if(i==region.cols){
						break;
					}
				} 
				_this.cols.push(i); 
			} 
			_this.colNum=_this.cols[0];	
		},
		colChange:function(e){
			let _this=this;
			_this.colNum=_this.cols[e.target.value];
			_this.colIndex=e.target.value;
		},	
		timingGetState:function(){
			let _this =this; 
			let qu_num=_this.regionNum;
			_this.submitAjax(_this.$mjjUrl+'denseShelves/getstates?quNum='+qu_num,null,'POST',function(res){ 
				console.log(res)
				if(res.data.state && res.data.row){ 
					let data=JSON.parse(res.data.row);
					_this.state=data.message; 	 
					// if(_this.state=='禁止'|| _this.state=='锁定'
					// 	|| _this.state=='到位'||_this.state=='机械锁定'||_this.state=='停止'){
					// 		_this.status.lock = '锁定';
					// 		_this.status.lockedUrl = '../../static/option-icon/locked.png';
					// 		_this.isLocked=true;
					// }
					// if(_this.state=='解除'||_this.state=='解锁'){ 
					// 		_this.status.lock = '解锁';
					// 		_this.status.lockedUrl = '../../static/option-icon/unlock.png';
					// 		_this.isLocked=false;
					// } 
				}
			})
			_this.getGethumiture(qu_num); 
		},
		// 获取温湿度
		getGethumiture:function(qu_num){
			let _this=this;
			_this.submitAjax(_this.$mjjUrl+'countmodule/storeAndRegionCount/getNewHumiture?quNum='+qu_num,null,'POST',function(res){ 
				if(res.data.state && res.data.row){
					let data=res.data.row;	
					// console.log(data)
					_this.hjz.wd=data.wd.toFixed(1)?data.wd.toFixed(1):0; 
					_this.hjz.sd=data.sd.toFixed(1)?data.sd.toFixed(1):0;
					_this.hjz.tvoc=data.tvoc.toFixed(1)?data.tvoc.toFixed(1):0; 
					_this.hjz.co2=data.co2.toFixed(1)?data.co2.toFixed(1):0;
					_this.hjz.ch20=data.ch20.toFixed(1)?data.ch20.toFixed(1):0;
					_this.hjz.pm10=data.pm10.toFixed(1)?data.pm10.toFixed(1):0;
					_this.hjz.pm25=data.pm25.toFixed(1)?data.pm25.toFixed(1):0; 
				} 
			})
		},
		// 解锁 锁定
		locked: function () {
			let _this=this;	
			let qu_num=_this.regionNum;
			if (_this.isLocked) { 
				_this.submitAjax(_this.$mjjUrl+'denseShelves/Unlock?quNum='+qu_num,null,'POST',function(res){
					console.log(res)
					if(res.data.state && res.data.row){
						let data=JSON.parse(res.data.row);
						_this.isLocked=false; 
					}else{
						
					} 
				}) 
			}else {  
				_this.submitAjax(_this.$mjjUrl+'denseShelves/locking?quNum='+qu_num,null,'POST',function(res){
					console.log(res)
					if(res.data.state && res.data.row){
						let data=JSON.parse(res.data.row);
						_this.isLocked=true; 
					}else{
						
					} 
				})
			}
		},
		// 自动开架
		opTask: function () {
			let _this=this; 
			_this.submitAjax(_this.$mjjUrl+'denseShelves/openshelf?quNum='+_this.regionNum,null,'POST',function(res){
				// console.log(res)
			})  
		},
		// 合架
		closeTask: function () {
			let _this=this; 
			_this.submitAjax(_this.$mjjUrl+'denseShelves/merge?quNum='+_this.regionNum,null,'POST',function(res){
				// console.log(res)
			})
		}, 
		// 通风
		ventilation: function () {
			let _this=this; 
			_this.submitAjax(_this.$mjjUrl+'denseShelves/aeration?quNum='+_this.regionNum,null,'POST',function(res){
				// console.log(res);
			})
		}, 
		// 左移
		left: function () {
			let _this=this;  
			// _this.direction='left';
			_this.submitAjax(_this.$mjjUrl+'denseShelves/leftmove?quNum='+_this.regionNum+'&column='+_this.colNum,null,'POST',function(res){
				if(res.data.state && res.data.row){
					let data=JSON.parse(res.data.row);
					// _this.curState=data.message; 
				}else{
					
				} 
			});
		},
		// 停止
		stop: function() {
			let _this=this;  
			_this.submitAjax(_this.$mjjUrl+'denseShelves/stop?quNum='+_this.regionNum,null,'POST',function(res){
				// console.log(res) 
			}) 
		},
		// 右移 
		right: function () {
			let _this=this; 
			// _this.direction='right';
			_this.submitAjax(_this.$mjjUrl+'denseShelves/rightmove?quNum='+_this.regionNum+'&column='+_this.colNum,null,'POST',function(res){
				if(res.data.state && res.data.row){
					let data=JSON.parse(res.data.row);
					// _this.curState=data.message;  
				}else{
					
				} 
			})
		}, 
	}
}
